  border-radius: 4px;
  outline: none;
  box-shadow: 0 0 3px #fd5103;
}

.header-wrap .auth .icon-signout, .header-wrap .auth.is-active .icon-signin {
  display: none;
}

.header-wrap .auth.is-active .icon-signout {
  display: block;
}

.nav {
