.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.switch input:checked + .slider {
  background-color: #fd5103;
}

.switch input:focus + .slider {
  box-shadow: 0 0 2px #fd5103;
}

.switch input:checked + .slider:before {
  background: var(--color-accent);
  transform: translateX(20px);
}

.slider {
  cursor: pointer;
  background-color: #0000;
  border: .6px solid #fd5103;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.slider:before {
  content: "";
  height: 15px;
  width: 15px;
  background: #fd5103;
  margin: auto 0;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 1px;
}

.slider.round {
  border: 1px solid var(--color-accent);
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

